<template>
   <div>
    <router-link
      tag="button"
      class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button custom-button--right custom-button--primary"
      :to="{ name: 'products.createView' }"
    >
      <i class="material-icons">add</i> Add
    </router-link>
    <div class="mdl-layout-spacer"></div>

    <data-table
      :columns="columns"
      :data="products.data"
      :actionMethod="fetchProducts"
      :count="products.count"
      :searchPlaceholder="'Search by Name'"
    ></data-table>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import { DataTable } from 'SHARED/data_table'
export default {
  name: 'products-list-view',
  components: {
    DataTable
  },
  computed: {
    ...mapGetters({
      products: 'getProducts'
    })
  },
  methods: {
    ...mapActions([
      'fetchProducts'
    ])
  },
  mounted () {
    this.fetchProducts()
  },
  data () {
    return {
      columns: [
        {
          field: 'name',
          label: 'Name',
          type: String
        },
        {
          field: 'price',
          label: 'Price',
          type: Number
        },
        {
          field: 'quantity',
          label: 'Quantity',
          type: String
        },
        {
          field: 'raw_material_balance',
          label: 'Stock',
          type: String
        },
        {
          field: 'is_active',
          label: 'Active',
          type: Boolean
        }
      ]
    }
  }
}
</script>



